import React, { useEffect, useRef, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { io } from 'socket.io-client';
import Peer from 'simple-peer';
import { useAuth } from '../context/AuthContext.js';
import { MdMic, MdMicOff, MdVideocam, MdVideocamOff, MdCallEnd } from 'react-icons/md';
import './VideoCall.css';

const RemoteVideo = ({ peer }) => {
    const ref = useRef();

    useEffect(() => {
        if (peer) {
            peer.on('stream', stream => {
                if (ref.current) {
                    ref.current.srcObject = stream;
                }
            });
            peer.on('error', (err) => console.error('Peer connection error:', err));
        }
    }, [peer]);

    return (
        <video
            playsInline
            autoPlay
            ref={ref}
            className="remote-video"
        />
    );
};

const LocalVideo = ({ stream }) => {
    const ref = useRef();

    useEffect(() => {
        if (stream && ref.current) {
            ref.current.srcObject = stream;
        }
    }, [stream]);

    return (
        <video
            playsInline
            autoPlay
            ref={ref}
            muted
            className="local-video"
        />
    );
};


const VideoCallPage = () => {
    const [peers, setPeers] = useState([]);
    const [localStream, setLocalStream] = useState(null);
    const [isMuted, setIsMuted] = useState(false);
    const [isCameraOff, setIsCameraOff] = useState(false);
    
    const socketRef = useRef();
    const peersRef = useRef([]);
    const localStreamRef = useRef();
    const { meetingId } = useParams();
    const navigate = useNavigate();
    const { user } = useAuth();
    
    const didInit = useRef(false);

    useEffect(() => {
        if (didInit.current) return;
        didInit.current = true;

        socketRef.current = io('http://localhost:5001');

        navigator.mediaDevices.getUserMedia({ video: true, audio: true })
            .then(stream => {
                setLocalStream(stream);
                localStreamRef.current = stream;

                socketRef.current.emit('join-room', meetingId, user.name);
                
                socketRef.current.on('other-user-in-room', otherUser => {
                    console.log('CLIENT: Other user found. I will initiate.');
                    
                    if (peersRef.current.find(p => p.peerID === otherUser.id)) return;
                    if (!localStreamRef.current) return;

                    const peer = new Peer({
                        initiator: true,
                        trickle: false,
                        stream: localStreamRef.current,
                    });

                    peer.on('connect', () => {
                        console.log(`INITIATOR: PEER CONNECTION ESTABLISHED WITH ${otherUser.id}`);
                    });

                    peer.on('signal', signal => {
                        socketRef.current.emit('offer', {
                            targetSocketID: otherUser.id,
                            callerID: socketRef.current.id,
                            signal,
                            username: user.name,
                        });
                    });

                    const peerObj = { peerID: otherUser.id, peer, username: otherUser.username };
                    peersRef.current.push(peerObj);
                    setPeers(prevPeers => [...prevPeers, peerObj]);
                });

                socketRef.current.on('offer-received', payload => {
                    console.log('CLIENT: Offer received from', payload.callerID);

                    if (peersRef.current.find(p => p.peerID === payload.callerID)) return;
                    if (!localStreamRef.current) return;
                    
                    const peer = new Peer({
                        initiator: false,
                        trickle: false,
                        stream: localStreamRef.current,
                    });
                    
                    peer.on('connect', () => {
                        console.log(`RECEIVER: PEER CONNECTION ESTABLISHED WITH ${payload.callerID}`);
                    });

                    peer.on('signal', signal => {
                        socketRef.current.emit('answer', {
                            signal,
                            targetSocketID: payload.callerID,
                            username: user.name,
                        });
                    });

                    peer.signal(payload.signal);

                    const peerObj = { peerID: payload.callerID, peer, username: payload.username };
                    peersRef.current.push(peerObj);
                    setPeers(prevPeers => [...prevPeers, peerObj]);
                });

                socketRef.current.on('answer-received', payload => {
                    console.log('CLIENT: Answer received.');
                    const item = peersRef.current.find(p => p.peerID === payload.callerID);
                    if (item) {
                        item.peer.signal(payload.signal);
                    }
                });

                socketRef.current.on('user-left', id => {
                    console.log(`CLIENT: User ${id} left.`);
                    const peerObj = peersRef.current.find(p => p.peerID === id);
                    if (peerObj) {
                        peerObj.peer.destroy();
                    }
                    const newPeers = peersRef.current.filter(p => p.peerID !== id);
                    peersRef.current = newPeers;
                    setPeers(newPeers);
                });

            })
            .catch(error => console.error('Error accessing media devices:', error));

        return () => {
            console.log("Cleanup function running for effect.");
            
            // **THE FIX IS HERE**: We DO NOT reset didInit.current here.
            // This ensures that the effect only runs its setup logic ONCE
            // for the component's entire lifetime on the page, even in Strict Mode.
            
            if (localStreamRef.current) {
                localStreamRef.current.getTracks().forEach(track => track.stop());
            }
            if (socketRef.current) {
                socketRef.current.disconnect();
            }
            peersRef.current.forEach(p => p.peer.destroy());
        };
    // eslint-disable-next-line
    }, []); 

    const toggleMute = () => {
        if (localStream) {
            const audioTrack = localStream.getAudioTracks()[0];
            audioTrack.enabled = !audioTrack.enabled;
            setIsMuted(!audioTrack.enabled);
        }
    };

    const toggleCamera = () => {
        if (localStream) {
            const videoTrack = localStream.getVideoTracks()[0];
            videoTrack.enabled = !videoTrack.enabled;
            setIsCameraOff(!videoTrack.enabled);
        }
    };

    const handleEndCall = () => navigate('/dashboard');

    const remotePeer = peers[0];

    return (
        <div className="call-container">
            <div className="video-area">
                <div className="remote-video-wrapper">
                    {remotePeer ? (
                        <RemoteVideo peer={remotePeer.peer} />
                    ) : (
                        <div className="waiting-placeholder">
                            <h2>Waiting for other user...</h2>
                        </div>
                    )}
                    {remotePeer && <div className="name-label">{remotePeer.username}</div>}
                </div>
                {localStream && (
                    <div className="local-video-wrapper">
                        <LocalVideo stream={localStream} />
                        <div className="name-label">{user?.name} (You)</div>
                    </div>
                )}
            </div>

            <div className="controls-bar">
                <button onClick={toggleMute} className={`control-btn ${isMuted ? 'active' : ''}`} title={isMuted ? "Unmute" : "Mute"}>
                    {isMuted ? <MdMicOff size={28} /> : <MdMic size={28} />}
                </button>
                <button onClick={toggleCamera} className={`control-btn ${isCameraOff ? 'active' : ''}`} title={isCameraOff ? "Turn Camera On" : "Turn Camera Off"}>
                    {isCameraOff ? <MdVideocamOff size={28} /> : <MdVideocam size={28} />}
                </button>
                <button onClick={handleEndCall} className="control-btn end-call-btn" title="End Call">
                    <MdCallEnd size={28} />
                </button>
            </div>
        </div>
    );
};

export default VideoCallPage;